.price__page {
    margin-top: 80px;
  }
  .price__page__content {
    margin-bottom: 50px;
  }
  .table__header {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 30px;
    padding-top: 50px;
  }
  .price__page__table {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 30px;
    padding-top: 10px;
  }
  
  @media only screen and (max-width: 375px) {
    .price__page__table {
      grid-template-columns: 1fr;
    }
    .table__header {
      grid-template-columns: 1fr;
      gap: 0;
      padding: 50px 15px 15px 15px;
    }
    .table__header span {
      display: none;
    }
    .price__page__table {
      grid-template-columns: 1fr;
      gap: 30px;
      padding: 10px;
    }
  }
  